import { FacebookFilled, InstagramFilled, LinkedinFilled, MailFilled, PhoneFilled, TwitterSquareFilled } from '@ant-design/icons'
import { Button, Col, Layout, message, Row, Space, Typography, Divider } from 'antd'
import React from 'react'
import { footer, header } from './data'
import './DefaultLayout.sass'
import Home from './Home'
import LeadCollectionForm from './LeadCollectionForm'
import logo from './logo.png'

const { Header, Content, Footer } = Layout
const { Paragraph } = Typography

class DefaultLayout extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            visible: false
        }
    }

    onCreate = values => {
        // console.log('Received values of form: ', values)
        message.success('Thank you for showing interest. We will reach out to you shorty.')
        this.setState({
            visible: false
        })
    }

    render() {
        const { visible } = this.state
        return (
            <Layout>
                <Header>
                    <img src={logo} alt="logo" width="120" />
                    <span className="logo">{header.logo}</span>
                    <Space size="large">
                        {
                            header.menu.map(item => (
                                <Button
                                    key={`menu-${item}`}
                                    style={{ color: "#000" }}
                                    type="link"
                                >
                                    {item}
                                </Button>
                            ))
                        }
                        <Button
                            onClick={() => {
                                this.setState({
                                    visible: true
                                })
                            }}
                            shape="round"
                            type="primary"
                        >
                            {header.btn_title.toUpperCase()}
                        </Button>
                    </Space>
                </Header>
                <Content className="site-layout">
                    <Home />
                </Content>
                <Footer>
                    <Row align="middle" justify="center">
                        <Col
                            md={12}
                            xs={20}
                        >
                            <Space direction="vertical" align="center" style={{ width: '100%' }}>
                                <Row justify="center">
                                    <img src={logo} alt="logo" width="150" />
                                </Row>
                                <Row justify="center">
                                    <Space>
                                        <Button
                                            href="https://www.linkedin.com/company/grassetto-studio-s/about/"
                                            icon={<LinkedinFilled />}
                                            size="large"
                                            type="link"
                                        />
                                        <Button
                                            href="https://www.facebook.com/Grassetto-Studio-101339888281434/"
                                            icon={<FacebookFilled />}
                                            size="large"
                                            type="link"
                                        />
                                        <Button
                                            href="https://www.instagram.com/grassetto_studio/"
                                            icon={<InstagramFilled />}
                                            size="large"
                                            type="link"
                                        />
                                        <Button
                                            href="https://twitter.com/grassettos"
                                            icon={<TwitterSquareFilled />}
                                            size="large"
                                            type="link"
                                        />
                                    </Space>
                                </Row>
                            </Space>
                        </Col>
                        <Col
                            md={12}
                            xs={20}
                        >
                            <Row justify="center">
                                <Space direction="vertical" size="large">
                                    <Paragraph strong>Contact us</Paragraph>
                                    <Space direction="vertical" size="small">
                                        <Paragraph>Call us</Paragraph>
                                        <Space>
                                            <PhoneFilled />
                                            <Button type="link" href="tel:+918660505609">+918660505609</Button>
                                        </Space>
                                    </Space>
                                    <Space direction="vertical" size="small">
                                        <Paragraph>Email us</Paragraph>
                                        <Space>
                                            <MailFilled />
                                            <Button type="link" href="mailto:hello@grassettostudio.in">hello@grassettostudio.in</Button>
                                        </Space>
                                    </Space>
                                </Space>
                            </Row>
                        </Col>
                    </Row>
                    <Row justify="center">
                        <Divider style={{ minWidth: '60%', width: '60%' }} />
                    </Row>
                    <Row justify="center">
                        <Paragraph>©2020 {footer.title}</Paragraph>
                    </Row>
                </Footer>
                <LeadCollectionForm
                    visible={visible}
                    onCreate={this.onCreate}
                    onCancel={() => {
                        this.setState({
                            visible: false
                        })
                    }}
                />
            </Layout>
        )
    }
}

export default DefaultLayout